$(document).ready(function () {
  var wow = new WOW();
  wow.init();
  $('.loader').delay(3000).fadeOut('slow');
  $('#overlayer').delay(3000).fadeOut('slow');
  $('.fullpage').fullpage({
    menu: '.menu',
    anchors: ['home', 'work', 'about'],
    autoScrolling: true,
    navigation: true,
    navigationTooltips: ['.home', '.work', '.about'],
    animateAnchor: true,
    showActiveTooltip: true,
    scrollingSpeed: 1000,
    animateAnchor: true,
    loopBottom: true,
    afterRender: function () {
      $('.work__item p').matchHeight(); // always one height
    },
  });
  particlesJS('particles-js', {
    particles: {
      number: {
        value: 70,
        density: {
          enable: true,
          value_area: 800,
        },
      },
      color: {
        value: '#ffffff',
      },
      shape: {
        type: 'circle',
        stroke: {
          width: 0,
          color: '#ffffff',
        },
        polygon: {
          nb_sides: 5,
        },
      },
      opacity: {
        value: 0.5,
        random: false,
        anim: {
          enable: false,
          speed: 1,
          opacity_min: 0.1,
          sync: false,
        },
      },
      size: {
        value: 3,
        random: true,
        anim: {
          enable: false,
          speed: 40,
          size_min: 0.1,
          sync: false,
        },
      },
      line_linked: {
        enable: true,
        distance: 150,
        color: '#ffffff',
        opacity: 0.4,
        width: 1,
      },
      move: {
        enable: true,
        speed: 6,
        direction: 'none',
        random: false,
        straight: false,
        out_mode: 'out',
        bounce: false,
        attract: {
          enable: false,
          rotateX: 600,
          rotateY: 1200,
        },
      },
    },
    interactivity: {
      detect_on: 'window',
      events: {
        onhover: {
          enable: false,
          mode: 'repulse',
        },
        onclick: {
          enable: false,
          mode: 'push',
        },
        resize: true,
      },
      modes: {
        grab: {
          distance: 400,
          line_linked: {
            opacity: 1,
          },
        },
        bubble: {
          distance: 400,
          size: 40,
          duration: 2,
          opacity: 8,
          speed: 3,
        },
        repulse: {
          distance: 200,
          duration: 0.4,
        },
        push: {
          particles_nb: 4,
        },
        remove: {
          particles_nb: 2,
        },
      },
    },
    retina_detect: true,
  });
  $('.work__item').tilt({
    perspective: 1200,
  });
  var i = 0;
  var txt =
    'Lorem ipsum, dolor sit amet consectetur adipisicing elit. Fuga deserunt, quo quae adipisci repellendus praesentium tempora dolores iste rem sunt!';
  function typeWriter() {
    if (i < txt.length) {
      document.getElementById('typetext').innerHTML += txt.charAt(i);
      i++;
      setTimeout(typeWriter, 40);
    }
  }
  setInterval(function () {
    typeWriter();
  }, 3000);
  $('.open_f').on('click', function () {
    var opnFilter = $('.filter').find('.filter__list');
    $(opnFilter).toggleClass('open-filter');
    $(this).toggleClass('anim-btn');
  });

  $('.work__block').isotope({
    itemSelector: '.work__item',
    layoutMode: 'fitRows',
  });
  $('.filter__list li a').on('click', function (e) {
    e.preventDefault();
    $('.filter__list li a').removeClass('active');
    $(this).addClass('active');

    var filterSelect = $(this).attr('data-filter');
    $('.work__block').isotope({
      filter: filterSelect,
    });
    return false;
  });
});
